













































































.index {
  height: 100%;
  display: flex;
}
.right {
  width: 100%;
}
.right2 {
  width: calc(100% - 240px);
}
.main {
  margin: 10px;
  background: #ffffff;
  height: calc(100% - 130px);
}
.el-scrollbar {
  /deep/.el-scrollbar__wrap {
    overflow-x: hidden;
    padding-right: 10px;
    box-sizing: border-box;
  }
}

/deep/.el-tabs__content {
  overflow: visible;
}
