












































































































.iconfont {
  margin-right: 18px;
  vertical-align: middle;
}
.header {
  background: #ffffff;
}
.header-list {
  display: flex;
}
.header-title {
  height: 60px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-size: 16px;
  padding: 0 20px;
  border: 1px solid #ddd;
  background: rgba(153, 204, 255, 1);
  color: #ffffff;
}
.umen-list .el-icon-message {
  margin-right: 14px;
}
.umen-list {
  color: #333;
}
.header-title > p {
  line-height: 60px;
}
.header-content {
  height: 32px;
  padding: 8px;
}
.top {
  display: flex;
}
.top-email {
  line-height: 68px;
  margin-right: 14px;
}
.top-email > a > i {
  font-size: 24px;
  color: #ffffff;
}
.title img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  vertical-align: middle;
}
.title-umen {
  width: 174px;
  height: 174px;
  background: #ffffff;
  position: absolute;
  top: 52px;
  z-index: 99;
  left: -94px;
  border: 1px solid #ddd;
  border-radius: 5px;
  box-shadow: -4px 4px 10px #ddd;
}
.umen-list > li {
  margin-top: 24px;
  padding: 0 34px;
}
.title {
  position: relative;
  // width: 80px;
  height: 40px;
  margin: 10px;
}
.title-buttom {
  position: absolute;
  top: -18px;
  left: 20px;
}

.el-dropdown-menu {
  .el-dropdown-menu__item {
    padding-left: 40px;
  }
}
